import "./App.css";
import { db } from "./firebase";
import { ref, onValue } from "firebase/database";
import { useState, useEffect } from "react";
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';

const theme = createTheme();

function App() {
  const [temps, setTemps] = useState([]);

  //read
  useEffect(() => {
    onValue(ref(db), (snapshot) => {
      setTemps([]);
      const data = snapshot.val();
      if (data !== null) {
        Object.values(data).map((temp) => {
          setTemps((oldArray) => [...oldArray, temp]);
        });
      }
    });
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />


      <div className="App">

        {temps.map((temp) => (
          <>
            <Container
              alignItems='center'
              sx={{
                pt: 2,
                pb: 2,
                mt: 10
              }}
              justifyContent='center'
              style={{ borderRadius: 20, color: 'white', backgroundColor: '#4287f5' }}
              component="main"
              maxWidth="xs">
              <Typography
                component="h1"
                variant="h3"
                align="center"
                gutterBottom>
                {temp.name}
              </Typography>

              <Typography
                component="h1"
                variant="h4"
                align="center"
                gutterBottom>
                {temp.C}&#176;C
              </Typography>

              <Typography
                component="h1"
                variant="h4"
                align="center"
                gutterBottom>
                {temp.F}&#176;F
              </Typography>

            </Container>
          </>
        ))}
      </div>
    </ThemeProvider>
  );
}

export default App;
